.calculate{
    text-align: center;
    margin: 0 auto;
    max-width: 50%;
}

.calculate label{
    text-align: left;
    display: block;
    color: aliceblue;
    margin-bottom: 5px;
    font-size: 25px;
    font-family: 'Arial';
}

.calculate input{
    width: 100%;
    min-height: 30px;
    box-sizing: border-box;
    font-size: 30px;
    margin-bottom: 30px;
}

.calculate button{
    min-height: 30px;
    font-size: 25px;
    background: #dbddff;
    border: 0;
    border-radius: 8px;
    cursor: pointer;
}